" class=\"organizations\" :default=\"requestedOrg\" :placeholder=\"'Select'\"\n                @input=\"selectOrgGroup\"></CustomSelect>\n            <label class=\"name-label mt-4\">E-Mail</label>\n            <IDEInput @getValue=\"setOwnerEmail\" type=\"mail\" :value=\"email\" info-text=\"\"\n                placeholder=\"Group owner's e-mail\" />\n            <label class=\"name-label mt-4\">Description</label>\n            <IDEInput @getValue=\"setDescription\" type=\"textarea\" :value=\"description\" info-text=\"\"\n                placeholder=\"Describe your object details\" />\n            <div class=\"organization-group-info\">\n                <i class=\"mdi mdi-information\"></i>\n                <span>If you need the contact information of the Group Owner, please reach out to the <a style=\"color: #757575\" href=\"mailto:plateau@softtech.com.tr\">Plateau Studio Team.</a></span>\n            </div>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button class=\"create-button\" :disabled=\"isMailValid\" @click=\"joinOrganizationGroup()\">SEND REQUEST</button>\n        </div>\n    </div>\n</template>\n<script setup lang=\"ts\">\nimport { computed, onBeforeMount, onMounted, ref } from 'vue';\nimport { container } from '../../../../../domain/core/diContainer';\nimport { IUseCaseExecutor } from '../../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../../domain/viewModel/IViewModel';\nimport IDEInput from \"../../application/IDEInput.vue\";\nimport CustomSelect from \"../../customSelect.vue\";\nimport { IOrganizationGroupUseCase } from '../../../../../domain/useCase/IOrganizationGroupUseCase';\nimport { IStudioRequestRequest } from '../../../../../../../common/qCloudTemp/request';\n\nconst emit = defineEmits([\"close\"]);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst organizationGroupUseCase = container.resolve<IOrganizationGroupUseCase>(IOrganizationGroupUseCase);\nconst user = ref(viewModel.user!);\nconst selectedOrganization = ref(viewModel.user!.selectedOrganization);\nconst email = ref(\"\");\nconst description = ref(\"\");\n\nconst organizationNames = ref<Array<string>>([]);\nconst isSelectedOrgOwned = selectedOrganization.value?.ownerUserIds ? selectedOrganization.value?.ownerUserIds.includes(user.value.userId) : selectedOrganization.value?.creatorUserId == user.value.userId;\nconst requestedOrg = ref<string | undefined>(\"\");\nconst isMailValid = computed(() => !email.value ? false : !/^(?=.{1,64}@)[a-zA-Z0-9!#$%&'*+/=?^_`{|}~-]+(\\.[a-zA-Z0-9!#$%&'*+/=?^_`{|}~-]+)*@[a-zA-Z0-9-]+(\\.[a-zA-Z0-9-]+)*(\\.[a-zA-Z]{2,})$/.test(email.value)); //RFC 5322-compliant 🤔\n\nonBeforeMount(() => {\n    getOrganizationNames();\n})\n\nfunction close() {\n    emit(\"close\");\n}\n\nfunction selectOrgGroup(orgGroupName: string) {\n    requestedOrg.value = orgGroupName;\n}\n\nfunction getOrganizationNames() {\n    organizationNames.value = user.value.organizations.filter(org => (org.ownerUserIds ? org.ownerUserIds?.includes(user.value.userId) : org.creatorUserId == user.value.userId) && !org.organizationGroupId).map(org => org.name);\n    requestedOrg.value = organizationNames.value[0]\n}\nfunction joinOrganizationGroup() {\n    const request: IStudioRequestRequest = {targetUserEmail: email.value, organizationName: requestedOrg.value, description: description.value, type: \"joinOrganizationGroup\"};\n    executor.execute(async () => {\n        await organizationGroupUseCase.sendJoinOrganizationGroupRequest(request);\n    },{loading: true});\n    close();\n}\nfunction setOwnerEmail(value: string) {\n    email.value = value\n}\nfunction setDescription(value: string) {\n    description.value = value\n}\n</script>\n<style scoped>\n* {\n    font-family: 'Roboto';\n}\n\n.container {\n    padding: 32px 20px 20px;\n}\n\n.organization-name-div {\n    margin-bottom: 52px\n}\n\n.name-label {\n    font-weight: 500;\n    font-size: 15px;\n    line-height: 18px;\n    color: #151233;\n    margin-bottom: 4px;\n}\n\n.organizaton-name {\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    padding: 8px 8px 8px 12px;\n    width: 100%;\n    font-size: 15px;\n    color: #212121;\n    outline: none;\n}\n\n.organizaton-name::placeholder {\n    color: #BCBCBC\n}\n\n.buttons-div {\n    display: flex;\n    justify-content: end;\n\n}\n\n.cancel-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #757575;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    margin-right: 4px;\n    border: none;\n    background: transparent;\n}\n\n.create-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #FFFFFF;\n    background: #151233;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: none;\n}\n\n.create-button:disabled {\n    color: #757575\n}\n\n.button-tooltip {\n    visibility: hidden;\n    position: absolute;\n    background: #757575;\n    color: #FFFFFF;\n    font-size: 11px;\n    font-family: 'Roboto';\n    text-align: center;\n    border-radius: 4px;\n    letter-spacing: 0.2px;\n    padding: 6px 8px 6px 8px;\n    top: 56px;\n    left: 63px;\n    opacity: 0;\n}\n\n.create-button:disabled:hover+.button-tooltip {\n    visibility: visible;\n    transition: opacity 0.3s ease-out 0.5s;\n    opacity: 1;\n}\n\n.organization-group-info {\n    padding: 5px 12px;\n    color: #757575;\n    font-size: 14px;\n    background: #F5F5FA;\n    border-radius: 4px;\n    display: flex;\n    align-items: center;\n    margin-left: auto;\n    width: 100%;\n    margin-top: 20px;\n}\n\n.organization-group-info i {\n    font-size: 24px;\n    color: #151233;\n    margin-right: 8px;\n}\n\n.organizations {\n    width: 100%;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 0px 12px;\n}\n\n</style>"],"sourceRoot":""}]);
